import React from 'react'
import Item from './item.js'

const About = () => {
    const aboutArray = [
        { class: 'iconfont icon-age', title: '', desc: 24 },
        { class: 'iconfont icon-sex', title: '', desc: '' },
        { class: 'iconfont icon-education', title: '', desc: ''},
        { class: 'iconfont icon-University', title: '', desc: ''},
        { class: 'iconfont icon-english', title: '', desc: 'CET-6'},
        { class: 'iconfont icon-coordinate', title: '', desc: ''},
        { class: 'iconfont icon-experience', title: '', desc: '1'},
        { class: 'iconfont icon-intention', title: '', desc: ''},
        { class: 'iconfont icon-state', title: '', desc: ''}
        ]
    return (
        <div className="about-container">
            <h1 className="about-title"></h1>
            <div className="about-content">
                {aboutArray.map((message) => <Item key={message.class} message={message} />)}
            </div>
        </div>
    )
}

export default About


